*{
  margin: 0;
  padding: 0;
}

.header{
  border-bottom: 1px solid #ccc;
  display: flex;
  padding: 5px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 10px;

}
.header  .arrow{
  display: none;
  margin-right: 10px;
  padding: 8px;
  border-radius: 100%;
  

}
.banner-btn{
  text-decoration: none;
  display: block;
  color: #F9B524;
  font-weight: bold;

}
.icon-search-mobile{
  display: none;
}
.header .arrow:hover{
  background-color: rgba(110, 108, 108, 0.1);
 
}

.header .inputs input[type="text"]{
  outline: none;
  border: none;
  flex-grow: 1;
  background-color: transparent;
  height: 100%;
  font-size: large;
  padding-left: 10px;
}
.header .inputs button{
  border: 0px;
  width: 15px;
  height: 15px;
  margin-right: 15px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 100%;
  color: white;
  background-color: gray;
}
.header .inputs .icon-search{
  cursor: pointer;
}
.header .car{
  display: flex;
  align-items: center;
}
.header .car{
  cursor: pointer;
}

.inputs{
 background-color: rgba(110, 108, 108, 0.1);
  display: flex;
  align-items: center;
  flex-basis: 55%;
  padding-bottom: 10px;
  padding-top: 10px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
  height: 30px;
  position: relative;
}
table{
  width: 100%;
  


}
table thead{
  width: 100%;
  border: 0px;
  margin: 0px;
 

}

table thead th{
  background-color: #F9B524;
  padding: 10px;
  color: white;
  text-align: left;
  border: 0px;
  margin: 0px;
}


table tbody tr{
  padding: 5px;
  font-size: 16px;

text-align: left;
  cursor: pointer;
}
table tbody tr td{
  padding: 10px;


}

.hover:hover{
  background-color: rgba(0, 0, 0, 0.5);
}
table tbody tr:hover{
  background-color: red
}

table tbody tr:nth-child(2n+1){
  background-color: rgba(110, 108, 108, 0.1);
}
table tbody tr:nth-child(2n+2){
  background-color: rgba(110, 108, 108, 0.5);
  color: white;
}
.search-container{
  position: absolute;
  width: 100%;
  background-color:white;
  box-shadow:  0px 0px 1px 1px #ccc;
  height: 200px;
  bottom: 0;
  top: 100%;
  margin-top: 10px;
  z-index: 999;
  left: 0;
  overflow-y: scroll;
}
.search-container::-webkit-scrollbar{
  width: 10px;
  background-color: gainsboro;


}

.search-container::-webkit-scrollbar-thumb{
  background-color: rgba(0, 0, 0, 0.3);
  width: 10px;
  
}
.flex{
    display: flex;
    margin: 0px 10px;
    height: 100vh;
  

 
}


.banner{
  flex-basis: 20%;
  border-right: 1px solid #ccc;

}
.main-content{
  flex-basis: 80%;
}

